.singleComment{
    display: flex;
    justify-content: space-evenly;
    margin-top: 5px;
    margin-bottom: 5px;
}
.singleComment img{
    width:5%;
    border-radius: 50%;
}
.singleComment .commentData{
    width:90%;
}
.singleComment .commentData h5{
    margin-bottom: 5px;
}
.singleComment .commentData .displayComment{
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding:5px;
}
.singleComment .commentData .displayComment textarea{
    border:none;
    resize: none;
    width: 100%;
    font-family:inherit;
    outline: none;
}
#close{
    border:none;
    font-size: 20px;
    font-family: inherit;
    background-color: inherit;
    color:grey;
    margin-left:10px;
}
a{
    font-size: 12px;
    margin: 5px;
}